$mainColor : #01686B;
$accentColor: #F46060;
.card-img-top {
  max-width: 22vh; }
#root {}
body {
  background: radial-gradient(circle, rgba(1,104,107,1) 55%, rgba(131,197,190,1) 100%); }
#imageIndex {
  img {
    padding: .7rem; } }
.navbar {
  width: 100%;
  a {
    width: 33%;
    display: inline;
    font-size: 2rem;
    color: #fff;

    &:hover {
      text-decoration: none;
      color: #AAC0AF;
      transition-duration: .8s; } }
  img {
    margin-left: 3rem;
    width: 10vh;
    align-self: start;
    &:hover {
      transform: {}
      transform: scale(1.5);
      transition-duration: .8s; } } }
.accentColorText {
  color: $accentColor; }
h1 {
  font-family: 'Syne', sans-serif; }
input {
  opacity: .7; }

button {
  background-color: $accentColor!important; }


